:root {
  --light-mode-text: hsl(200, 15%, 8%);
  --light-mode-bg: hsl(0, 0%, 98%);
  --light-mode-input: hsl(0, 0%, 52%);
  --light-mode-elements: hsl(0, 0%, 100%);
}

:root {
  --dark-mode-text: hsl(0, 0%, 100%);
  --dark-mode-elements: hsl(209, 23%, 22%);
  --dark-mode-bg: hsl(207, 26%, 17%);
}

.detail-main .container {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  align-items: flex-start;
  width: 90%;
  gap: 5rem;
}
.detail-main .container button {
  display: flex;
  flex-flow: row nowrap;
  gap: 0.5rem;
  padding: 0.5rem 1.8rem;
  border-radius: 5px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}
.detail-main .container button img {
  width: 20px;
}

.details {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  gap: 5rem;
}

.details > * {
  flex: 1 1 40%;
}

.border-countries {
  flex-flow: row wrap !important;
  justify-content: flex-start !important;
  align-items: baseline !important;
  gap: 1rem;
}
.border-countries p {
  font-size: 1rem;
  font-weight: 600;
}

.details-container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  gap: 3.5rem;
}
.details-container h2 {
  font-size: 1.9rem;
  font-weight: 800;
}
.details-container div {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
  row-gap: 1rem;
  max-height: 150px;
  width: 100%;
}
.details-container div p {
  overflow-wrap: break-word;
  font-size: 1rem;
}
.details-container div p strong {
  font-weight: 600;
}

.lightBG {
  background-color: var(--light-mode-bg);
}

.darkBG {
  background-color: var(--dark-mode-bg);
}

.light-theme {
  color: var(--light-mode-text);
  background-color: var(--light-mode-bg);
}
.light-theme header {
  background-color: var(--light-mode-elements);
}
.light-theme form {
  background-color: var(--light-mode-elements);
}
.light-theme .country-card {
  background-color: var(--light-mode-elements);
}

.dark-theme {
  color: var(--dark-mode-text);
  background-color: var(--dark-mode-bg);
}
.dark-theme button {
  color: var(--dark-mode-text);
  background-color: var(--dark-mode-elements);
}
.dark-theme button img {
  filter: invert(100%);
}
.dark-theme header {
  background-color: var(--dark-mode-elements);
}
.dark-theme form {
  background-color: var(--dark-mode-elements);
}
.dark-theme .country-card {
  background-color: var(--dark-mode-elements);
}
.dark-theme ::placeholder {
  color: var(--dark-mode-text) !important;
}
.dark-theme .search-form input {
  color: var(--dark-mode-text) !important;
}
.dark-theme .custom-select, .dark-theme .select-selected, .dark-theme .select-items div {
  color: var(--dark-mode-text) !important;
  background-color: var(--dark-mode-elements) !important;
}
.dark-theme .select-selected:after {
  border-color: rgb(255, 255, 255) transparent transparent transparent !important;
}
.dark-theme .select-selected.select-arrow-active:after {
  border-color: transparent transparent rgb(255, 255, 255) !important;
}

@media screen and (max-width: 425px) {
  .details-container div {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    align-items: flex-start;
    gap: 1rem;
    row-gap: 1rem;
    height: auto;
    max-height: 50%;
    width: 100%;
  }
  .details-container div :nth-child(5) {
    margin-top: 2rem;
  }
}
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 768px) and (max-width: 912px) {
  .details {
    top: 10rem;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 3rem;
  }
  .details > * {
    flex: 0 1 100%;
  }
}