/* BASIC ****************************************************** */
*,
*:before,
*:after {
  box-sizing: inherit;
}

/* * {
  outline: 1px solid #f00 !important;
  opacity: 1 !important;
  visibility: visible !important;
} */
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  min-height: 100% !important;
  height: 100%;
  font-size: 14px;
  overflow-x: hidden;
  overflow-y: auto;
}

body,
main,
#root,
#page-wrapper {
  height: 100%;
  font-family: "Nunito Sans", sans-serif;
}

#page-wrapper,
#info-group,
main {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

#page-wrapper, body, html {
  height: auto;
}

#page-wrapper {
  align-items: flex-start;
  gap: 3rem;
}

/*  ******************************************************* */
button {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 5px;
  font-weight: 600;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: transparent;
  text-decoration: none;
}
button img {
  width: 20px;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}
header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  gap: 1.4rem;
  padding: 1.5rem 0;
}
header nav h1 {
  font-size: 1.3rem;
  font-weight: 800;
}

main {
  width: 100%;
  gap: 3rem;
  padding-bottom: 5rem;
}
main .form-section {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  gap: 1rem;
}
main .form-section form {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  border-radius: 5px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

.search-form {
  width: 40%;
  min-width: 300px;
}
.search-form input {
  width: 100%;
  outline: none;
  border: none;
  background-color: transparent;
}

.countries-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  gap: 1rem;
  row-gap: 5rem;
  width: 100%;
  height: auto;
}

.country-card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  width: 20%;
  min-width: 250px;
  cursor: pointer;
  border-radius: 5px;
  padding: 0 0 1rem 0;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}
.country-card img {
  width: 100%;
  height: auto;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.country-card p {
  font-size: 1.3rem;
  font-weight: 800;
  align-self: flex-start;
  padding: 0 1rem;
}

.country-card-details {
  width: 100%;
}
.country-card-details p {
  font-size: 1rem;
  font-weight: 300;
  align-self: flex-start;
  padding: 0 1rem 0.5rem 1rem;
}
.country-card-details p strong {
  font-weight: 600;
}

:root {
  --light-mode-text: hsl(200, 15%, 8%);
  --light-mode-bg: hsl(0, 0%, 98%);
  --light-mode-input: hsl(0, 0%, 52%);
  --light-mode-elements: hsl(0, 0%, 100%);
}

:root {
  --dark-mode-text: hsl(0, 0%, 100%);
  --dark-mode-elements: hsl(209, 23%, 22%);
  --dark-mode-bg: hsl(207, 26%, 17%);
}

.lightBG {
  background-color: var(--light-mode-bg);
}

.darkBG {
  background-color: var(--dark-mode-bg);
}

.light-theme {
  color: var(--light-mode-text);
  background-color: var(--light-mode-bg);
}
.light-theme header {
  background-color: var(--light-mode-elements);
}
.light-theme form {
  background-color: var(--light-mode-elements);
}
.light-theme .country-card {
  background-color: var(--light-mode-elements);
}

.dark-theme {
  color: var(--dark-mode-text);
  background-color: var(--dark-mode-bg);
}
.dark-theme button {
  color: var(--dark-mode-text);
  background-color: var(--dark-mode-elements);
}
.dark-theme button img {
  filter: invert(100%);
}
.dark-theme header {
  background-color: var(--dark-mode-elements);
}
.dark-theme form {
  background-color: var(--dark-mode-elements);
}
.dark-theme .country-card {
  background-color: var(--dark-mode-elements);
}
.dark-theme ::placeholder {
  color: var(--dark-mode-text) !important;
}
.dark-theme .search-form input {
  color: var(--dark-mode-text) !important;
}
.dark-theme .custom-select, .dark-theme .select-selected, .dark-theme .select-items div {
  color: var(--dark-mode-text) !important;
  background-color: var(--dark-mode-elements) !important;
}
.dark-theme .select-selected:after {
  border-color: rgb(255, 255, 255) transparent transparent transparent !important;
}
.dark-theme .select-selected.select-arrow-active:after {
  border-color: transparent transparent rgb(255, 255, 255) !important;
}